import React, { useEffect } from 'react';
import { navigate } from 'gatsby'

import styles from './navBar.css';

const NavBar = () => {
    let isPortfolioPage = false;
    let scrollToPortfolio = () => { };

    useEffect(() => {
        isPortfolioPage = window.location.pathname.includes('portfolio');
        scrollToPortfolio = () => isPortfolioPage
            ? navigate('/')
            : window.scrollTo({
                top: 600,
                behavior: 'smooth'
            });

    }, []);

    return (
        <div className="nb-root">
            <div>
                <div className="nb-name">Kinga Gryz</div>
                <div>UX/UI Designer</div>
            </div>

            <div className="nb-navItems">
                <div onClick={scrollToPortfolio} className="nb-navItem">
                    {isPortfolioPage ? 'Home' : 'Portfolio'}
                </div>
                <div href="mailto:kingagryz7@gmail.com" className="nb-navItem">
                    kingagryz7@gmail.com
                </div>
            </div>
        </div>
    )
}

export default NavBar;