import React from 'react';
import { Link } from 'gatsby';

import Layout from "../Layout";

import PortfolioContentSection from "./PortfolioContentSection";
import styles from './portfolioItemLayout.css'

const PortfolioItemLayout = ({
    mainImgSrc,
    title,
    subtitle,
    // listing
    sector,
    myRole,
    technology,
    projectTime,
    // First section
    firstSectionTitle,
    firstSectionDescription,
    // rest
    contentSections,
    nextLink,
}) => (
    <Layout>
        <div>
            <div className="pil-mainHeaderContainer">
                <img className="pil-mainHeaderImage" src={mainImgSrc} />
                <div className="pil-mainHeaderTextContainer">
                    <h1 className="pil-mainHeader">{title}</h1>
                    <p className="pil-mainSubheader">{subtitle}</p>
                </div>
                <div className="pil-mainHeaderShadow" />
            </div>

            <div className="pil-itemBody">
                <div className="pil-dataLines">
                    <div className="pil-dataLine">
                        <div className="pil-label">Sector</div><div>{sector}</div>
                    </div>
                    <div className="pil-dataLine">
                        <div className="pil-label">My role</div><div>{myRole}</div>
                    </div>
                    <div className="pil-dataLine">
                        <div className="pil-label">Technology</div><div>{technology}</div>
                    </div>
                    <div className="pil-dataLine">
                        <div className="pil-label">Project Time</div><div>{projectTime}</div>
                    </div>
                </div>

                <div className="pil-firstSection">
                    <h3 className="pil-firstSectionTitle">{firstSectionTitle}</h3>
                    <div className="pil-firstSectionDescription">{firstSectionDescription}</div>
                </div>

                <div>
                    {contentSections.map((section, index) => <PortfolioContentSection {...section} isReversed={index % 2} />)}
                </div>

                <div className="pil-buttons">
                    <Link to="/">
                        <div className="pil-button">Back To Home</div>
                    </Link>
                    <Link to={nextLink}>
                        <div className="pil-button">Go To Next Case</div>
                    </Link>
                </div>
            </div>

        </div>
    </Layout>
)

export default PortfolioItemLayout;